/* Import Bootstrap */
@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css');

/* Background */
.search-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(135deg, #404050, #34373a);
  overflow: hidden;
  padding: 20px;
}

/* Header with Filters */
header {
  background: #343a40;
  color: white;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 20px;
}

header h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  text-align: center;
}

header p {
  font-size: 1.2rem;
  color: #ddd;
  text-align: center;
}

.header-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.header-filters .filter-input {
  flex: 1 1 auto;
  max-width: 200px;
  padding: 10px;
  border-radius: 8px;
  border: none;
  background: #ffffff1a;
  color: white;
}

.header-filters .filter-input::placeholder {
  color: #ddd;
}

.header-filters .filter-btn {
  background: black;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  transition: background 0.3s ease-in-out;
  cursor: pointer;
}

.header-filters .filter-btn:hover,
.header-filters .filter-btn:active {
  background: gray;
}

/* Dog Grid */
.dog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  width: 100%;
  padding: 20px;
}

/* Pagination */
.sch_pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 20px;
}

.sch_pagination-btn {
  background: black;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  transition: background 0.3s ease-in-out;
  cursor: pointer;
}

.sch_pagination-btn:hover,
.sch_pagination-btn:active {
  background: gray;
}

.sch_pagination-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
}

/* Modal Styling */
.modal-content {
  background: #343a40;
  color: white;
  border-radius: 10px;
}

.modal-header,
.modal-footer {
  border: none;
}

.modal-footer .btn {
  background: black;
  color: white;
  border: none;
  transition: background 0.3s ease-in-out;
}

.modal-footer .btn:hover {
  background: gray;
}

/* Mobile Filter Button */
.mobile-filter-btn {
  display: none;
  position: fixed;
  top: 10px;
  left: 10px;
  background: black;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 50%;
  z-index: 1000;
  cursor: pointer;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 30px;
  font-size: 20px;
}

@media (max-width: 768px) {
  .mobile-filter-btn {
    display: block;
  }

  .header-filters {
    flex-direction: column;
    align-items: stretch;
  }

  .header-filters .filter-input {
    max-width: 100%;
  }

  .dog-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

/* Sidebar Filters */
.sidebar {
  background-color: #f8f9fa24;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
}

.sidebar.open {
  display: block;
}

/* Animation for filter container */
@keyframes slideIn {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

.sidebar {
  animation: slideIn 0.5s ease-in-out;
}

/* Animation for filter */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.sch_filter {
  animation: fadeIn 0.3s ease-in-out;
}

/* Pagination Button Styles */
.pagination .page-item .page-link {
  background-color: black;
  color: white;
  border: none;
  margin: 0 5px;
  border-radius: 8px;
  transition: background 0.3s ease-in-out;
}

.pagination .page-item .page-link:hover {
  background-color: gray;
}

.pagination .page-item.disabled .page-link {
  background-color: #ccc;
  color: #666;
}

.pagination .page-item.active .page-link {
  background-color: #dc3545;
  color: white;
}

/* Input Styling */
input.form-control {
  padding: 10px !important;
  color: wheat;
  background: #ffffff1a;
  border: none;
}

input.form-control::placeholder {
  color: #ddd;
}

/* Match Result */
.match-result {
  margin-top: 20px;
  text-align: center;
}

/* Responsive Design */
@media (max-width: 768px) {
  .search-container {
    padding: 10px;
  }

  .sidebar {
    width: 100%;
    max-width: none;
    border-right: none;
  }

  .dog-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }

  .sch_pagination {
    flex-direction: column;
    gap: 10px;
  }

  .sch_btn,
  .sch_page-btn {
    width: 100%;
  }
}